import React from 'react'
import HeadImg from '../../assets/our-company-global-operations.png'
import OilRig from '../../assets/oil-rig.png'
import DrillingOperations from '../../assets/drilling-operations.png'
import dangerousHTML from './Helpers'

const GlobalOperations = props => {
  return (
    <div>
      <div className="page-head-img img">
        <img src={HeadImg} />
        <span>North Malay Basin Full Field Development, Malaysia</span>
      </div>
      <div className="inner-content">
        <h1 dangerouslySetInnerHTML={dangerousHTML(props.title)} />
        <section>
          <h4>Production</h4>
          <p>
            Hess During 2017, the company sold higher cost, mature assets in the
            Permian, Equatorial Guinea and Norway, consistent with our strategy
            to high grade and focus our portfolio on our highest return assets.
            Net production from assets sold in 2017 totaled approximately 50,000
            barrels of oil equivalent per day.
          </p>
          <p>
            In 2017, net production averaged 306,000 barrels of oil equivalent
            per day, including Libya, compared with 322,000 barrels of oil
            equivalent per day in 2016. The decline in production year over year
            was primarily the result of asset sales associated with the
            strategic reshaping of the company’s portfolio, unplanned downtime
            resulting from a fire at the Shell-operated Enchilada platform in
            the Gulf of Mexico and natural field declines.
          </p>
          <p>
            In the Bakken, Hess’ operated rig count averaged 3.5 in 2017,
            slightly higher than the average rig count of 3 in 2016. While the
            company brought 68 new wells on production in 2017 compared with 100
            wells in 2016, net production from the Bakken was flat year on year,
            at 105,000 barrels of oil equivalent per day.
          </p>
          <p>
            Based on positive production results from our 60-stage,
            140,000-pound proppant per stage completion design, we expect to
            achieve an average 10-15 percent uplift in estimated ultimate
            recovery and 180-day cumulative initial production rates per well,
            compared with our previous 50-stage and 70,000-pound proppant per
            stage completion design. As a result, we have increased our estimate
            of net ultimate recovery from our Bakken acreage to 2.0 billion
            barrels of oil equivalent from our previous estimate of 1.7 billion
            barrels of oil equivalent.
          </p>
          <p>
            In the Utica shale play in eastern Ohio, where the company
            participates in a 50 percent joint venture with CONSOL Energy, net
            production averaged 19,000 barrels of oil equivalent per day in
            2017, compared to 29,000 barrels of oil equivalent per day in 2016,
            reflecting natural field declines, as Hess did not bring any new
            wells online in 2017. In the deepwater Gulf of Mexico, net
            production averaged 54,000 barrels of oil equivalent per day in
            2017, compared with 61,000 barrels of oil equivalent per day in
            2016. This decrease is primarily the result of unplanned downtime
            resulting from a fire at the Shell-operated Enchilada platform and
            natural field declines, partially offset by a 7,000 barrels of oil
            equivalent per day increase from the Tubular Bells Field (57 percent
            working interest, operator). At the Carigali Hess-operated
            Malaysia/Thailand Joint Development Area (50 percent working
            interest) net production averaged 223 million cubic feet of gas
            equivalent per day in 2017, compared with 206 million cubic feet of
            gas equivalent per day in 2016. In the Danish North Sea, net
            production from the South Arne Field (62 percent working interest,
            operator) averaged 10,000 barrels of oil equivalent per day in 2017,
            compared with 13,000 barrels of oil equivalent per day in 2016,
            reflecting natural field declines. In the fourth quarter of 2017,
            the company commenced a sales process for our interests in Denmark,
            which is expected to be completed in 2018.
          </p>
        </section>
        <section className="img two">
          <img src={OilRig} />
          <span>Production Operations, North Dakota</span>
        </section>
        <section>
          <h4>Developments</h4>
          <p>
            At the North Malay Basin full field development project in the Gulf
            of Thailand (50 percent working interest, operator), Hess completed
            installation of the central processing platform and achieved first
            gas in July 2017 safely, on time and under budget. The completion of
            the full field development resulted in net production averaging 66
            million cubic feet equivalent per day in 2017, compared with 28
            million cubic feet equivalent per day in 2016. In the fourth quarter
            of 2017, North Malay Basin reached its planned plateau rate of
            approximately 165 million cubic feet per day of gas net to Hess,
            establishing the asset as a significant long-term, low-cost cash
            generator for the company.
          </p>
          <p>
            In the deepwater Gulf of Mexico, Hess continued to advance the
            Stampede development project (25 percent working interest,
            operator). In 2017, the company successfully installed the tension
            leg platform and topsides on location, completed the development’s
            first three production wells and completed all subsea work. First
            oil was achieved in January 2018.
          </p>
          <p>
            Offshore Guyana, Hess holds a 30 percent interest in the
            6.6-million-acre Stabroek Block. Esso Exploration and Production
            Guyana Limited, a subsidiary of ExxonMobil is operator and holds 45
            percent interest. CNOOC Nexen Petroleum Guyana Limited holds the
            remaining 25 percent interest.
          </p>
          <p>
            In June 2017, the first phase of the Liza Development, on the
            Stabroek Block, was sanctioned. The project will develop, on a gross
            basis, approximately 450 million barrels of oil through a floating
            production, storage and offloading (FPSO) vessel with capacity of
            approximately 120,000 barrels of oil per day. First production from
            the Liza Phase 1 development is expected in March 2020. Development
            drilling is planned to start in 2018 using the Noble Bob Douglas
            drillship.
          </p>
          <p>
            Planning for a second phase of development at Liza is under way and
            expected to utilize an FPSO with production capacity of
            approximately 220 thousand barrels of oil per day. First oil from
            the Liza Phase 2 development is targeted for mid-2022. A third phase
            of development will focus on the Payara area and is expected to
            closely follow Liza Phase 2.
          </p>
        </section>
        <section className="img three">
          <img src={DrillingOperations} />
          <span>Drilling Operations, Offshore Guyana</span>
        </section>
        <section>
          <h4>Exploration</h4>
          <p>
            The world-class Liza-1 discovery in 2015 and subsequent exploration
            and appraisal drilling in 2016 brought total discovered resource on
            the Stabroek Block, offshore Guyana to more than 1 billion barrels
            of oil equivalent at the end of 2016. In 2017, another oil discovery
            on the block was confirmed at the Snoek well located approximately
            five miles southeast of the Liza-1 discovery. The Snoek well
            commenced drilling on February 22, 2017 and was drilled to 16,978
            feet in 5,128 feet of water. The Snoek exploration well encountered
            82 feet of high-quality, oil-bearing sandstone reservoirs.
          </p>
          <p>
            Following the Snoek exploration well, the Payara-2 appraisal well
            was drilled in the fourth quarter of 2017 and encountered 59 feet of
            high-quality, oil bearing sandstone reservoirs. The Payara-2
            appraisal well confirmed Payara as a significant oil discovery.
          </p>
          <p>
            A fifth oil discovery on the Stabroek Block, was announced at the
            Turbot-1 prospect located approximately 30 miles southeast of the
            Liza Phase 1 project. The Turbot-1 exploration well began drilling
            on August 14, 2017 and encountered a reservoir of 75 feet of
            high-quality, oil-bearing sandstone reservoirs. Drilling of an
            additional well on the Turbot discovery is planned for 2018.
          </p>
          <p>
            The five discoveries on the block – Liza, Payara, Snoek, Liza Deep
            and Turbot – are now estimated to have total recoverable resources
            of more than 3.2 billion barrels of oil equivalent, nearly triple
            the estimate from this time last year.
          </p>
          <p>
            Esso Exploration and Production Guyana Limited, a subsidiary of
            ExxonMobil, began drilling the Ranger-1 exploration well on November
            5, 2017 using the Stena Carron drillship to 21,161 feet depth in
            8,973 feet of water. The well was a discovery and encountered
            approximately 230 feet of high-quality, oil-bearing carbonate
            reservoir. Resources associated with the Ranger-1 discovery will be
            accretive to the more than 3.2 billion barrels of oil equivalent
            previously discovered on the block. Additional exploration drilling
            is planned on the Stabroek Block for 2018, including appraisal
            drilling at the Ranger discovery.
          </p>
          <p>
            In Suriname, Hess, Chevron and Kosmos (operator) hold a one-third
            interest in the 1.3-million-acre Block 42. Interpretation of 3D
            seismic is underway with a first exploration well on the block
            planned for the second half of 2018. In the 2.8-million-acre Block
            59, where Hess also holds a 33 percent interest together with
            Statoil and ExxonMobil (operator), plans are underway to acquire 2D
            seismic in 2018.
          </p>
        </section>
      </div>
    </div>
  )
}

export default GlobalOperations



